@import "../../layout/constants.less";
@import "../../partials/message.less";

.chatMain {
  padding-top: 32px;
}

.chatTyper {
  position: fixed;
  display: flex;
  width: 375px;
  bottom: 0;
  height: 55px;
  background-color: @primary;

  align-items: center;
}

.sendButtonCase {
  position: absolute;
  display: flex;
  right: 16px;
  bottom: 16px;
  width: 70px;
  height: 70px;
  border: 4px solid #FFFFFF;
  border-radius: 50%;
  background-color: @primary;

  z-index: 1;

  justify-content: center;
  align-items: center;
}

.addFilesIconBtn {
  margin: 0 16px;
  padding: 0;
}

.addImageIconBtn {
  margin: 0 16px 0 0;
  padding: 0;
}

.chatInput {
  width: 192px;
  padding: 8px 0;
  background-color: @primary;
  outline: none;
  border: none;

  color: white;
}
.chatInput::placeholder {
  color: white;
}
